body {
  background-color: #9266ff;
}
.App {
  background-color: #3f3163;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}
#to-do-form input {
  background-color:rgb(27,112,137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 5px;
  color: aliceblue;
}
#to-do-form input::placeholder {
  color:gainsboro;
}
#to-do-form input:focus, #to-do-form button {
  outline: none;
}
#to-do-form button{
  color:whitesmoke;
  height: 50px;
  font-size: 15px;
  font-weight:bolder;
  width: 80px;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
  background-color:darkgoldenrod ;
  
}